import { useEffect } from 'react';

import { Container, Content, Drawer, Section } from 'components/layout';
import { Button, Typography } from 'components/ui';

import './index.scss';
// ----------------------------------------------------------------------

interface ErrorFallbackProps {
  error: Error;
  resetErrorBoundary: () => void;
}

const ErrorFallback = ({ error, resetErrorBoundary }: ErrorFallbackProps) => {
  useEffect(() => {
    console.error('error ', error);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Drawer>
      <Container>
        <Content>
          <Section>
            <div className="qb-error">
              <div className="qb-error__message">
                <Typography variant="head" size="xl">
                  We are under scheduled maintenance.
                </Typography>
                <Typography>Sorry for the inconvenience, we will be back shortly!</Typography>
              </div>
              <div className="qb-error__actions">
                <Button onClick={resetErrorBoundary}>확인</Button>
              </div>
            </div>
          </Section>
        </Content>
      </Container>
    </Drawer>
  );
};

export default ErrorFallback;
